.travel {
  width: 33%;
  margin-right: 10px;;
}

.travel__title {
  color: $titles;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
}

.travel__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  list-style: none;
  padding: 0;
}

.travel__element {
  width: 33%;
  margin-bottom: 11px;

  color: $details-very-light;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  // margin-right: 50px;
}
