@import "../variables.scss";

.cost {
  display: flex;
  flex-direction: column;
  margin-right: 30px;
  padding: 38px 28px 28px 28px;

  width: 370px;
  min-height: 672px;

  border: 1px solid $titles-very-light;
  border-radius: 6px;
}

.cost--active {
  border: 3px solid $accent;
  border-radius: 6px;
}

.cost__title {
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: -1.27273px;
}

.cost__value {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 34px;
}

.cost--active .cost__name {
  color: $accent;
}

.cost--active .cost__title {
  line-height: 34px;
  letter-spacing: -0.2px;
}

.cost__name {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 23px;

  color: $details-light;
}

.cost__description {
  margin-bottom: 60px;
  padding-right: 41px;

  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.cost__list {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style: none;
  padding-left: 30px;

  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
}

.cost__element {
  margin-bottom: 8px;
}

.cost__link {
  margin-top: 22px;
  padding: 30px;
  min-height: 75px;
  background-color: $accent;
}

.cost__buy {
  display: block;
  color: $white;
  text-align: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.cost--active .cost__link {
  margin-top: 46px;
}
