.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 24px 49px;
}

.form__button {
  margin-top: 24px;
  margin-left: calc(50% - 88px);

  width: 176px;
  min-height: 50px;

  color: $white;
  background-color: $accent;
  border: none;

  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.form__input {
  width: 269px;
  min-height: 50px;

  padding-left: 13px;
  border: 1px solid $titles-very-light;

  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
