@import "../variables.scss";

.page-header {
  display: flex;
  justify-content: space-between;

  min-height: 99px;

  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: $details
}

.page-header__logo {
  width: 42%;
  margin-left: 2px;
}

.page-header__nav {
  display: flex;
  margin-left: 160px;
  margin-top: 5px;
}

.page-header__link {
  width: 29%;
  margin-top: 36px;
  margin-right: 7px;

  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
  text-decoration: underline;
}
