.questions {
  margin: 122px 363px 0 387px;
  width: 844px;
  height: 455px;
  background-color: $accent;
  background-image: url(../../img/questions-background.jpg);
}

.questions__wrapper {
  display: inline-block;
  margin-top: 70px;
  margin-left: 61%;

  width: 656px;
  min-height: 315px;

  background-color: $white;
  box-shadow: 0px 4px 30px $shadow;
  border-radius: 5px;
}

.questions__title {
  margin-top: 50px;
  margin-bottom: 12px;
  color: $titles;
  text-align: center;
  font-weight: 600;
  font-size: 25px;
  line-height: 32px;
}

.questions__description {
  margin-top: 0;
  color: $titles;
  text-align: center;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}
