@import "../variables.scss";

.contacts {
  width: 33%;
}

.contacts__title {
  margin-left: 89px;
  margin-bottom: 20px;

  color: $titles;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
}

.contacts__link {
  display: block;

  margin-left: 89px;
  margin-bottom: 20px;
  padding-left: 40px;

  color: $accent;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-decoration-line: underline;
}

.contacts__link--phone {
  font-size: 16px;
  line-height: 32px;
}
