.main-information {
  min-height: 860px;
  background-color: $background-blue;
  background-image: url(../../img/main-background-desktop.jpg);
  color: $white;
}

.main-information__wrapper {
  margin: 0;
  margin-left: auto;
  margin-right: 417px;
  width: 412px;
  padding-top: 140px;
}

.main-information__title {
  margin-bottom: 27px;

  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  letter-spacing: -1.10667px;
}

.main-information__description {
  max-width: 376px;

  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: -0.466667px;
}

.main-information__link {
  display: block;
  margin-top: 35px;
  background-color: $accent;
  width: 340px;
  min-height: 75px;
}

.main-information__button {
  display: block;
  padding: 30px;
  padding-left: 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
}
