.prices {
  margin: -283px 345px 0 375px;
  display: flex;
}

.prices__about {
  width: 383px;
  padding: 72px 0 0 80px;
}

.prices__title {
  margin-bottom: 48px;
  color: $titles;
  font-weight: 600;
  font-size: 55px;
  line-height: 90px;
}

.prices__description {
  margin-bottom: 60px;
  color: $titles-light;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
}

.prices__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}

.prices__contact {
  margin-bottom: 13px;
  text-align: center;
  color: $titles;
  font-weight: 500;
  font-size: 22px;
  line-height: 34px;
}

.prices__link {
  display: block;
  text-align: center;
  color: $black;
  font-weight: normal;
  font-size: 20px;
  line-height: 32px;
}
