@import "../variables.scss";

.buy-tour {
  display: none;
  // display: grid;
  grid-template-columns: 338px 390px;
  grid-template-rows: 165px auto;
  width: 728px;
  height: 444px;

  background-color: $background-modal;
}

.buy-tour__picture {
  grid-row: 1/3;
  width: 338px;
  height: 444px;
}

.buy-tour__title {
  grid-row: 1;
  grid-column: 2;
  margin-top: 72px;
  margin-bottom: 0;

  color: $modal-text;
  text-align: center;
  font-weight: 600;
  font-size: 25px;
  line-height: 43px;
}

.buy-tour__form {
  grid-row: 2;
  grid-column: 2;

  margin: 0 51px;
}


.modal-form__input {
  width: 270px;
  height: 50px;

  margin-bottom: 20px;
  padding-left: 13px;
  border: 1px solid $titles-very-light;

  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.modal-form__button {
  margin-left: calc(50% - 88px);

  width: 176px;
  min-height: 50px;

  color: $white;
  background-color: $accent;
  border: none;

  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
}
