@import "../../sass/variables.scss";

.country {
  display: grid;
  grid-template-columns: 41% 40% 20%;
  grid-template-rows: min-content min-content 1fr;
  min-height: 1088px;

  margin: 0 137px;
}

.country__title {
  margin-top: 11px;
  margin-bottom: 9px;

  color: $titles;
  font-weight: 600;
  font-size: 55px;
  line-height: 90px;
  letter-spacing: -1.245px;
}

.country__description {
  grid-column: 1;
  margin-right: 100px;

  color: $titles-light;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.466667px;
}

.country__link {
  grid-column: 1;
}

.country__picture {
  grid-column: 2;
  grid-row: 1/4;

  width: 430px;
  height: 596;
}

.country__link {
  margin-top: 8px;

  width: 250px;
  min-height: 75px;
  height: min-content;

  background-color: $accent;
}

.country__buy {
  display: inline-block;
  padding: 30px;
  padding-left: 40px;
  color: $white;

  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.country__review {
  grid-column: 3;
  grid-row: 1/3;
  margin-left: -145px;
  margin-top: 115px;
  width: 382px;
  min-height: 367px;
  background-color: $white;
}
